import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Card, Form, Input } from 'antd';
import DatePickerString from 'components/common/input/DatePickerString';
import { getDocInId, createDocIn, updateDocIn } from 'services/docIn';
import { requiredRule } from 'utils/validateRules';

const initialValueDefault = {
  code: '',
  esId: '',
  firstname: '',
  lastname: '',
  dob: '',
  pobId: '',
  gender: '',
  schoolId: '',
  areaId: '',
  graduatedYear: '',
  studyField: '',
  testGroup: '',
  gradeM1: '',
  gradeM2: '',
  gradeM3: '',
  certType: '',
  trainTypeId: '',
};

@Form.create({ name: 'CandidateStudentFormPage' })
class CandidateStudentFormPage extends Component {
  state = {
    dataId: false,
  };

  componentDidMount = async () => {
    const { match, form } = this.props;
    if (match.params.id) {
      this.setState({ dataId: match.params.id });
      const initialValue = await this.getDataById(match.params.id);
      form.setFieldsValue(initialValue);
    } else {
      form.setFieldsValue(initialValueDefault);
    }
  };

  getDataById = async id => {
    const res = await getDocInId(id);
    return { ...initialValueDefault, ...res.data };
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      console.log('values fffff', values);
      this.saveData(values);
    });
  };

  saveData = async doc => {
    const { dataId } = this.state;
    try {
      if (!dataId) {
        await createDocIn(doc);
      } else {
        await updateDocIn(doc);
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    return (
      <Card title="Thêm ứng viên">
        <Form {...formItemLayout} onSubmit={this.handleSubmit}>
          <Form.Item label="Số CV đến">
            {getFieldDecorator('docInNo', { rules: [requiredRule] })(<Input />)}
          </Form.Item>
          <Form.Item label="Đến ngày">
            {getFieldDecorator('docInDate', { rules: [requiredRule] })(
              <DatePickerString placeholder="Chọn ngày" />
            )}
          </Form.Item>
          <Form.Item label="Nơi gửi">
            {getFieldDecorator('sentPlace', { rules: [requiredRule] })(<Input />)}
          </Form.Item>
          <Form.Item label="Số vào sổ">
            {getFieldDecorator('id', { rules: [requiredRule] })(<Input />)}
          </Form.Item>
          <Form.Item label="Ngày vào">
            {getFieldDecorator('noteDate', { rules: [requiredRule] })(
              <DatePickerString placeholder="Chọn ngày" />
            )}
          </Form.Item>
          <Form.Item label="Nội dung">
            {getFieldDecorator('docContent', { rules: [requiredRule] })(<Input />)}
          </Form.Item>
          <Form.Item label="Nơi lưu">{getFieldDecorator('savedPlace')(<Input />)}</Form.Item>
          <Form.Item label="ĐV ký nhận">{getFieldDecorator('recipientUnit')(<Input />)}</Form.Item>
          <div style={{ textAlign: 'center' }}>
            <BtnSave htmlType="submit" />
            <BtnCancel onClick={() => this.props.history.goBack()} />
          </div>
        </Form>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <CandidateStudentFormPage {...props} />
  </Page>
));



